.checkout_form{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    
    padding: 20px;
    background-color: white;
    height: max-content;
}
.checkout_ad{
    width: 100%;
    margin-bottom: 15px;
}
.checkout_title{
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}